"use client"
import React, { useEffect, useState } from "react";
import { TransaksiProps } from "@/interfaces";
import { CategoryOption, TransactionFormData } from "@/interfaces/ITransaction";
import { fetchAllCategories } from "@/services/category";

import convertNumRupiah from "@/utils/convertNumberRupiah"
const TransactionForm: React.FC<TransaksiProps> =  ( {initialData, onSubmit}) => {
    const[ form,setForm ] = useState<TransactionFormData>({
        type: "expense",
        amount: "",
        date: new Date().toISOString().slice(0, 10),
        note: "",
        categoryId: "",
    });

    const[categories, setCategories] = useState<CategoryOption[]>([])
    const loadCategories = async () => {
        try {
            const res = await fetchAllCategories();
            setCategories(res.data)
        }catch(error) {
            if(error instanceof Error ) {
                console.error({message: error.message, type: "danger"})
            }else {
                console.error({ message: "terjadi kesalahan", type: "danger" });
            }
        }
    }
useEffect(() => {
    loadCategories();
}, [])
    return (
      <form action="" className="space-y-4 max-w-xl">
        <div>
          <label htmlFor="tipe" className="block text-sm mb-1">
            <select
              name="type"
              value=""
              className="w-full border px-3 py-2 rounded"
            >
              <option value="income"> pemasukan</option>
              <option value="expense"> pengeluaran</option>
            </select>
          </label>
        </div>
        <div>
          <label htmlFor="jumlah" className="block text-sm mb-1">
            jumlah
          </label>
          <input
            type="text"
            name="amount"
            value=""
            className="w-full border px-3 py-2 rounded"
            placeholder="contoh Rp10.000"
            required
          />
        </div>

        <div>
          <label htmlFor="tanggal" className="block text-sm mb-1">
            tanggal
          </label>
          <input
            type="date"
            name="date"
            value=""
            className="w-full border px-3 py-2 rounded"
            placeholder="contoh Rp10.000"
            required
          />
        </div>
        <div>
          <label htmlFor="catatan" className="block text-sm mb-1">
            catatan
          </label>
          <input
            type="note"
            name="note"
            value=""
            className="w-full border px-3 py-2 rounded"
            placeholder="contoh Rp10.000"
            required
          />
        </div>

        <div>
          <label htmlFor="catatan" className="block text-sm mb-1">
            catatan
          </label>
          <select
            name="categoryId"
            value=""
            className="w-full border px-3 py-2 rounded"
            required
          >
            <option value="">----pilih kategori-----</option>
            {categories.map((category) => (
                <option key={category.id} value={category.id}>
                {category.name}
                </option>
            ))}
          </select>
        </div>
        <button 
        type="submit" className="bg-indigo-600 text-white px-4 py-2 rounded hover:bg-indigo-700">simpan</button>
      </form>
    );
}

export default TransactionForm